.header {
  width: 96%;
  margin: 0 0 0 0;
  padding: 6px 2% 6px 2%;
  font-family: "trebuchet MS", Verdana, sans-serif;
  color: #fff;
  background-color: #555555;
  h1 {
    font-size: 2.4em;
    margin: 0;
    color: #fff;
  }
}

.content {
  margin: 0 0 0 2%;
  position: relative;
  .content__container {
    background: #fff;
    width: 96%;
    margin-top: 8px;
    padding: 10px;
    position: relative;
  }
  fieldset {
    padding: 0 15px 10px 15px;
  }
  h2 {
    font-size: 1.7em;
    margin: 0;
    color: #cc0000;
  }
  h3 {
    font-size: 1.2em;
    margin: 10px 0 0 0;
    color: #000000;
  }
}
